.searcbarForm {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.searcbarForm input[type='text'] {
  padding: 8px 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 200px;
  outline: none;
}
.searcbarForm button {
  margin-left: 8px;
  padding: 8px 16px;
  background-color: rgb(102, 30, 3);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
